import React, { useState,useEffect } from 'react'
import { Link,useNavigate } from 'react-router-dom'
import {message} from 'antd';

function Header() {
  const [loginUser,setLoginUser]=useState("");
  const nav=useNavigate();
  const logout=()=>{
    message.success('logout Successful');
    localStorage.removeItem('user');
    nav('/');
  }
  useEffect(()=>{
    const user=JSON.parse(localStorage.getItem('user'));
    if(user){
      setLoginUser(user);
    }
  },[])
  return (
    <>
    <nav className="nav">
      <ul>
        <h1>Expense Management</h1>
        
         { !localStorage.getItem('user') ?<div className='user '><li className='login cta'> <Link to='/login' style={{color:'white'}}>Login</Link></li>
         <li className='register cta'><Link to='/register' style={{color:'white'}}>Register</Link></li></div> :
         <>
         <li>UserName: {loginUser.name}</li>
         <li><button onClick={logout} className='btn'>Logout</button></li>
         </>}
      </ul>
    </nav>
    </>
  )
}

export default Header