import React,{useState,useEffect} from 'react'
import Layout from '../components/Layout'
import axios from 'axios'
import { Modal,Form,Input,Select,message,Table} from 'antd'
function HomePage() {
  const [showModal,setShowModal]=useState(false);
  const [alltransaction,setalltransaction]=useState([]);
 
  // table data
  const columns=[
    {
      title:'Date',
      dataIndex:'date'
    },
    {
      title:'Amount',
      dataIndex:'amount'
    },
    {
      title:'Type',
      dataIndex:'type'
    },
    {
      title:'Category',
      dataIndex:'category'
    },
    {
      title:'Reference',
      dataIndex:'reference'
    },
    {
      title:'Description',
      dataIndex:'description'
    },
  ]
  //getall Transaction
  const getAlltransaction=async()=>{
    try{
    const user=JSON.parse(localStorage.getItem('user'));
    const res=await axios.post('/api/v1/trans/get-transaction',{userid:user._id});
      console.log(res.data);
    setalltransaction(res.data);
    }catch(err){
      console.log(err);
      message.error('not fetching data');
    }
  }
  //formsubmit handle
  const formSubmit=async(value)=>{
    console.log(value);
    try{
      const user=JSON.parse(localStorage.getItem('user'));
      await axios.post('/trans/add-transaction',{...value,userid:user._id});
      message.success('saved successfully');
    }catch(err){
      console.log(err);
      message.error('Not save! somethings went wrong');
    }
   
  }
  useEffect(()=>{
    getAlltransaction();
  },[])
  return (
    <Layout >
    <div>
      <div className='addNew'><button onClick={()=>setShowModal(true)} className='btn'>Add New</button></div>
    </div>
    <div className='content'>
    <Table columns={columns} dataSource={alltransaction} />
    </div>
    <Modal title='Add transaction' open={showModal} onCancel={()=> setShowModal(false)}
    footer={false}>
      <Form layout='vertical' onFinish={formSubmit}>
        <Form.Item label='Amount' name='amount'>
          <Input type='text'/>
        </Form.Item>
        <Form.Item label='Type' name='type'>
        <Select>
          <Select.Option value='income'>Income</Select.Option>
          <Select.Option value='expense'>Expense</Select.Option>
        </Select>
        </Form.Item>
        <Form.Item label='Category' name='category'>
        <Select>
          <Select.Option value='salary'>Salary</Select.Option>
          <Select.Option value='other'>Other</Select.Option>
          <Select.Option value='cloth'>Cloth</Select.Option>
          <Select.Option value='food'>Food</Select.Option>
          <Select.Option value='chai-biscuits'>chai-biscuits</Select.Option>
        </Select>
        </Form.Item>
        <Form.Item label='Date' name='date'>
          <Input type='date'/>
        </Form.Item>
        <Form.Item label='Reference' name='reference'>
          <Input type='text'/>
        </Form.Item>
        <Form.Item label='Description' name='description'>
          <Input type='text'/>
        </Form.Item>
        <div className='Btn'>
          <button type='submit'>SAVE</button>
        </div>
        
      </Form>
    </Modal>
    
    </Layout>
  )
}

export default HomePage